import { ListViewComponent } from './list-view.component';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'formatItems'})
export class SelectedListFormatPipe implements PipeTransform {

    constructor( private listViewRef: ListViewComponent ) {}

    transform(data: any, formatter: any): any {
        if (data) {

            const idField = this.listViewRef.listidName;

            if (formatter) {
                data = data.map( d => {
                    return {id: d[idField], text:  formatter(d) };
                });
            } else  {
                data = data.map((d: any) => {
                    const txt = [];
                    const keys = Object.keys(d);
                    keys.forEach(k => {
                        txt.push(d[k]);
                    });
                    return {id: d[idField], text: txt.join(', ') };
                });
            }

            return data;
        }

        return [];
    }
}
