import { FarrisCommonModule } from '@farris/ui-common';
import { LocaleModule } from '@farris/ui-locale';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { PaginationModule } from '@farris/ui-pagination';
import { ListViewComponent } from './list-view.component';
import { ListViewCheckoutComponent } from './component/list-view-checkout.component';

import {
    ListViewHeaderTemplateDirective,
    ListViewTemplateDirective,
    ListViewFooterTemplateDirective,
    ListViewEmptyTemplateDirective
} from './template.directive';
import { SelectedListFormatPipe } from './selected-listitem-formatter.pipe';


@NgModule({
    declarations: [
        ListViewComponent,
        ListViewCheckoutComponent,
        ListViewHeaderTemplateDirective,
        ListViewTemplateDirective,
        ListViewFooterTemplateDirective,
        ListViewEmptyTemplateDirective,
        SelectedListFormatPipe,
    ],
    imports: [
        CommonModule,
        FormsModule,
        LocaleModule.forRoot(),
        FarrisCommonModule,
        PaginationModule
    ],
    exports: [
        ListViewComponent,
        ListViewCheckoutComponent,
        ListViewHeaderTemplateDirective,
        ListViewTemplateDirective,
        ListViewFooterTemplateDirective,
        ListViewEmptyTemplateDirective,
        SelectedListFormatPipe
    ]
})
export class ListViewModule { }
